import React, { Component } from 'react';
import cover from '../assets/cover.png';
import { bounceInLeft } from 'react-animations';
import Radium, {StyleRoot} from 'radium';
import { Instagram ,Facebook,Linkedin} from 'react-feather';
import MediaQuery from 'react-responsive'


class Cover extends Component {
    state = {  }
    render() { 
        const styles={
            coverBackground : {
                backgroundColor:"#222831",
                height:700,
                width:"100%",
                overflow:"hidden",
                margin:0
               },

            coverTitle : {
                padding:100,
                color:"white",
                animation: 'x 5s',
                animationName: Radium.keyframes(bounceInLeft, 'bounce-in-left')
               },

            listStyle:{
                list:{
                    margin:20,
                    
                }
            }
        };

        return ( 
            <React.Fragment>
                <div className="row" style={styles.coverBackground}>
                    <div className="col-sm">
                    <ul style={{ listStyleType: "none",position:"fixed",marginTop:200,padding:0}}>
                        <li style={styles.listStyle.list}><Instagram color="white" size={20}/></li>
                        <li style={styles.listStyle.list}><Facebook color="white" size={20}/></li>
                        <li style={styles.listStyle.list}><Linkedin color="white" size={20}/></li>
                    </ul>
                    </div>
                    <MediaQuery minDeviceWidth={1000}>
                        <div className="col-sm">
                            <StyleRoot style={styles.coverTitle}>
                                <h1 style={{fontSize:100}}>I'm <span style={{color:"#29a19c"}}>TJ</span>,</h1>
                                <h1 style={{fontSize:100}}>Full Stack Mobile Developer</h1>
                            </StyleRoot>
                        </div>
                    </MediaQuery>

                    <MediaQuery minDeviceWidth={300} maxDeviceWidth={999}>
                        <div className="col-sm">
                            <StyleRoot style={styles.coverTitle}>
                                <h1 style={{fontSize:50,paddingTop:100}}>I'm <span style={{color:"#29a19c"}}>TJ</span>,</h1>
                                <h1 style={{fontSize:50}}>Full Stack Mobile Developer</h1>
                            </StyleRoot>
                        </div>
                    </MediaQuery>
                </div>
            </React.Fragment>
         );
    }
}
 
export default Cover;