import React, { Component } from 'react';
import DevIcon from "devicon-react-svg";
import profile from "../assets/profile.png";
import { Zap} from 'react-feather';
import MediaQuery from 'react-responsive';

class AboutUs extends Component {
    state = { 
        icons:[
            {id:1,name:"js_badge",hover:false},
            {id:2,name:"nodejs",hover:false},
            {id:3,name:"java",hover:false},
            {id:4,name:"php",hover:false},
            {id:5,name:"react",hover:false},
            {id:6,name:"html5",hover:false},
            {id:7,name:"css3_full",hover:false},
            {id:8,name:"firebase",hover:false},
            {id:9,name:"mysql",hover:false},
        ]
     }

     trigger=(icon)=>{
        let icons1=[...this.state.icons];
        const index= icons1.indexOf(icon);
        icons1[index].hover=!icons1[index].hover;
        console.log(icon.id);
        return icons1[index].hover;
    }

    render() { 


        const styles={
            section:{
                height:"auto",
                padding:50,
                backgroundColor:"#222831",
            },
            devIconStyle:{
                fill: this.trigger?"white":"grey",
                width: "50px",
                margin:20
            },
            boxShadow:{
                boxShadow: "1px 3px 1px #000000",
                borderRadius:"10px"
            }
        };
        


        return ( 
            <React.Fragment>
                <div style={styles.section}>
                    <div className="row" >
                        <div className="col-sm">
                            <div style={{borderWidth:10,borderStyle:"solid",borderColor:"#29a19c", margin:50,}}>
                                <img  style={{marginTop:-50,marginLeft:30,padding:20,flex: 1,width: null,height: null,resizeMode:"cover",width:"100%"}} src={profile} alt=""/>
                            </div>
                        </div>
                        <div className="col-sm">
                            <div className="container pd-2">
                            <MediaQuery minDeviceWidth={300} maxDeviceWidth={583}>
                                    <div className="m-2 p-2 justify-content-center" style={styles.boxShadow} >
                                        <h1 className="text-center text-white">5 IDENTITIES OF ME</h1>
                                        <ul style={{listStyle:"none"}}>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Malaysia-based developer</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Freelance Project Manager</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Software Engineer Degree Student</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Lifetime Traveler</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Maybe your business partner? :)</li>
                                        </ul>
                                    </div>
                                </MediaQuery>
                                <MediaQuery minDeviceWidth={584} maxDeviceWidth={2000}>
                                    <div className="m-2 p-5 justify-content-center" style={styles.boxShadow} >
                                        <h1 className="text-center text-white">5 IDENTITIES OF ME</h1>
                                        <ul style={{listStyle:"none"}}>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Malaysia-based developer</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Freelance Project Manager</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Software Engineer Degree Student</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Lifetime Traveler</li>
                                            <li className="m-2 text-white"><Zap color="white" size={20}/> Maybe your business partner? :)</li>
                                        </ul>
                                    </div>
                                </MediaQuery>
                            </div>
                        </div>
                        </div>
                </div>
            </React.Fragment>
         );
    }
}
 
export default AboutUs;