import React, { Component } from 'react';

class Navbar extends Component {
    state = {  }
    render() { 
        
        const navBackground={
            backgroundColor:"#222831",
            width:"100%",
            textDecoration: 'none'
        }

        return (
        <React.Fragment>
            <nav className="navbar navbar-expand-lg navbar-light" style={navBackground}>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-center" id="navbarNav">
                    <ul className="navbar-nav">
                    <li className="nav-item active">
                        <a className="m-2 text-white" href="#">Home <span className="sr-only">(current)</span></a>
                    </li>
                    <li className="nav-item">
                        <a className="m-2 text-white" href="#">About Me</a>
                    </li>
                    <li className="nav-item">
                        <a className="m-2 text-white" href="#">Portfolio</a>
                    </li>
                    <li className="nav-item">
                        <a className="m-2 text-white" href="#" tabIndex="-1" aria-disabled="true">Contact Me</a>
                    </li>
                    </ul>
                </div>
            </nav>
        </React.Fragment>
         );
    }
}
 
export default Navbar;