import React, { Component } from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar'
import DevIcon from "devicon-react-svg";
import MediaQuery from 'react-responsive';


class Proficiency extends Component {
    state = { 
        icons:[
            {id:1,name:"js_badge",hover:false},
            {id:2,name:"nodejs",hover:false},
            {id:3,name:"java",hover:false},
            {id:4,name:"php",hover:false},
            {id:5,name:"react",hover:false},
            {id:6,name:"html5",hover:false},
            {id:7,name:"css3_full",hover:false},
            {id:8,name:"firebase",hover:false},
            {id:9,name:"mysql",hover:false},
        ]
     }

     trigger=(icon)=>{
        let icons1=[...this.state.icons];
        const index= icons1.indexOf(icon);
        icons1[index].hover=!icons1[index].hover;
        console.log(icon.id);
        return icons1[index].hover;
    }

    render() { 


        const styles={
            section:{
                height:"auto",
                padding:50,
                backgroundColor:"#222831",
            },
            devIconStyle:{
                fill: "white",
                width: "50px",
                margin:20
            },
            boxShadow:{
                boxShadow: "1px 3px 1px #3b3a36",
                borderRadius:"10px"
            }
        };

        return ( 
            <React.Fragment>
            <div style={styles.section}>
                <div className="row" >
                    <div className="col-sm">
                        <div style={styles.section}>
                            <div className="container">
                                <h1 className="text-center text-white">Proficiency</h1>
                                <div className="m-2">
                                    <span  className="text-white">Frontend/Design</span>
                                    <ProgressBar variant="custom" animated now={45} />
                                </div>
                                <div className="m-2">
                                    <span  className="text-white">Backend</span>
                                    <ProgressBar variant="custom" animated now={80} />
                                </div>
                                <div className="m-2">
                                    <span className="text-white">Programming</span>
                                    <ProgressBar variant="custom" animated now={85} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm">
                    <MediaQuery minDeviceWidth={300} maxDeviceWidth={2000}>
                        <div className="container pd-5 m-5">
                            <h2 className="text-white text-center">What i do</h2>
                            <h4 className="text-white text-center">FULL STACK DEVELOPER WHO <span style={{color:"#29a19c"}}>EAGER TO LEARN, EXPLORE AND MASTER</span></h4>
                            <div className="m-2 justify-content-center" style={styles.boxShadow} >
                            {this.state.icons.map(icon=> <DevIcon onMouseEnter={()=>this.trigger(icon)}  icon={icon.name} key={icon.id} style={styles.devIconStyle} viewBox="0 0 32 32"/>)}
                            </div>
                        </div>
                    </MediaQuery>
                   
                        
                    </div>
                </div>
            </div>
        </React.Fragment>
         );
    }
}
 
export default Proficiency;